.proekt{
    margin-top: 44px;
    display: flex;
}
.selectize-input.focus > input{
    width: 100%!important;
}
.filter__title{
    font-weight: bold;
    font-size: 24px;
    line-height: 36px;
    color: rgba(0, 0, 0, 0.8);
    margin-bottom: 48px;
}

.filter__tabs{
    display: flex;
}
.filter__tab{
    font-weight: bold;
    font-size: 18px;
    line-height: 28px;
    color: #828282;
    margin-right: 42px;
    margin-bottom: 32px;
}

.filter__tab:last-child{
    margin-right: 0;
}
.filter__tab.active, .filter__tab:hover{
    color: #000000;
}

@media(min-width: 1450px){
    .proekt{
        margin-left: -32px;
    }
}

.filter__label{
    font-weight: bold;
    font-size: 18px;
    line-height: 24px;
    letter-spacing: 0.1px;
    display: block;
    color: #737B7D;
    margin-bottom: 13px;
}

.filter{
    flex: 0 0 300px;
    margin-right: 73px;
}

.input-search{
    position: relative;
    input{
        width: 100%;
        height: 48px;
        padding-left: 50px;
        margin-bottom: 0;
    }
    button{
        top: 50%;
        left: 13px;
        transform: translateY(-50%);
        position: absolute;
        background-color: transparent;
        border: 0;
        width: 35px;
        height: 35px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 100%;
        padding: 0;
    }
    button:hover{
        background-color: #F1F6FA;
    }
}

.filter__row{
    margin-bottom: 27px;
}
.filter__checkboxes{
    margin-bottom: 63px;
    .form-check-input{
        width: 1.3em;
        height: 1.3em;
        border: 2px solid #C3CBCD
    }
    .form-check-input:checked{
        border: 2px solid #0d6efd;
    }
    .form-check-label{
        font-size: 14px;
        line-height: 24px;
        color: #505565;
        padding-left: 8px;
        padding-top: 1px;
    }
    .form-check{
        margin-bottom: 10px;
    }
}

.filter__links{
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 0;
    padding-top: 15px;
}
.filter__links li{
    list-style: none;
    margin-bottom: 24px;
}
.filter__links a{
    font-size: 18px;
    line-height: 24px;
    letter-spacing: 0.1px;
    color: #737B7D;
    font-weight: bold;
    transition: 0.3s all;
}

.filter__links a:hover{
    color: #000;
}

.proekt__right{
    flex: 1;
}

.proekt__detail{
    background-color: #fff;
    border-radius: 15px;
}

.proekt__search{
    max-width: 633px;
}

.search__tags{
    display: flex;
    flex-wrap: wrap;
    margin-top: 8px;
}

.search__tag{
    padding: 4px 10px;
    background: #DBEEFB;
    border-radius: 40px;
    font-weight: bold;
    font-size: 10px;
    line-height: 1.4;
    display: flex;
    align-items: center;
    color: #3C64B1;
    margin-right: 7px;
    margin-bottom: 10px;
    position: relative;
    button{
        background-color: transparent;
        border: 0;
        padding: 0;
        margin-left: 10px;
    }
}

.proekt__search{
    margin-bottom: 32px;
}

.proekt__name{
    padding: 41px 33px;
    font-weight: bold;
    font-size: 32px;
    line-height: 40px;
    color: #0A243F;
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #E0E4EF;;
}
.proekt__actios{
    margin-left: 15px;
    display: flex;
    svg{
        min-width: 13.2px;
        min-height: 13.2px;
    }
    .active-svg{
        display: none;
    }
}

.selectize-input.dropdown-active::before {
    display: none;
}

.proekt__actios button{
    background: #FFFFFF;
    box-shadow: 4px 4px 6px rgba(0, 0, 0, 0.25);
    border: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 11px;
    padding: 0;
    width: 32px;
    height: 32px;
    border-radius: 100%;
    color: #000;
}

.proekt__actios button:hover, .proekt__actios button.active{
    background-color: #F1F6FA;
    color: var(--color-blue);
}

.proekt__actios button.active{
    svg{
        display: none;
    }
    .active-svg{
        display: block;
    }
}

.proekt__content{
    padding-top: 24px;
    padding-right: 33px;
    padding-left: 33px;
}
.proekt__content__top{
    display: flex;
}
.proekt__content__left{
    flex: 1;
    padding-right: 15px;
}
.proekt__content__status{
    font-weight: bold;
    font-size: 18px;
    line-height: 28px;
    color: #000000;
    margin-bottom: 4px;
}
.selectize-control.single .selectize-input {
    width: 100%;
    height: 48px;
   
    margin-bottom: 0;
    padding: 0 14px;
    border: 1px solid #d7d6dc;
    -webkit-box-shadow: 0 1px 3px rgba(0,0,0,.15);
    box-shadow: 0 1px 3px rgba(0,0,0,.15);
    border-radius: 20px;
    background: #fff;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    background-image: url('../img/proekt/search.svg');
    background-size: 17.5px;
    background-position: left 19px  center;
    background-repeat: no-repeat;
    padding-left: 50px;
}

.selectize-control.single .selectize-input.dropdown-active {
    border-radius: 20px 20px 0 0;
    border: 2px solid #2b5cfb;
    padding-left: 50px;
}
.selectize-input,.selectize-control.single .selectize-input.input-active {
    display: flex;
    background-color: #fff;
}
.selectize-input>input {
    font-size: 15px;
    line-height: 160%;
    
}
.selectize-input>input::placeholder{
    color: #AFACB9;
}
.selectize-input .item {
    font-size: 15px;
    line-height: 160%;
    letter-spacing: -.015em;
    color: rgba(0,0,0,.8);
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    -o-text-overflow: ellipsis;
    text-overflow: ellipsis;
    overflow: hidden
}

// .selectize-control.single .selectize-input.dropdown-active .item {
//     display: none
// }

.selectize-control.single .selectize-input:after {
    border-color: #a1a4b1 transparent transparent transparent
}

.selectize-dropdown {
    background: #fff;
    border: 1px solid #d7d6dc;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    -webkit-box-shadow: 0 1px 3px rgba(0,0,0,.15);
    box-shadow: 0 1px 3px rgba(0,0,0,.15);
    border-radius: 4px;
    overflow: hidden
}

.selectize-control {
    height: 48px;
    
}
.selectize-control.single .selectize-input:after{
    display: none;
}
.selectize-dropdown [data-selectable] {
    font-size: 15px;
    line-height: 24px;
    letter-spacing: -.015em;
    color: rgba(0,0,0,.6)
}

.selectize-dropdown [data-selectable] .highlight {
    color: #000;
    background-color: transparent
}
.selectize-search{
    position: relative;
}

.selectize-search--another-border{
    .selectize-control.single .selectize-input{
        border-radius: 10px;
    }
    .selectize-control.single .selectize-input.dropdown-active {
        border-radius: 10px 10px 0 0;
    }
}
.custom-select--another-border{
    .select-selected{
        border-radius: 10px;
        border: 1px solid #d7d6dc;
        box-shadow: 0 1px 3px rgb(0, 0, 0,15%);
    }
    .select-selected.select-arrow-active{
        border-radius: 10px 10px 0px 0px;
    }
}


.proekt__content__avatar{
    width: 48px;
    min-width: 48px;
    height: 48px;
    border-radius: 100%;
    overflow: hidden;
    background-size: cover;
    background-position: center;
    margin-right: 12px;
}
.proekt__content__person{
    display: flex;
    align-items: center;
    margin-bottom: 9px;
}
.proekt__content__person__name{
    font-size: 16px;
    line-height: 24px;
    color: #000000;
}

.proekt__tags{
    display: flex;
    align-items: flex-start;
    margin-bottom: 10px;
    flex-wrap: wrap;
}
.proekt__tags__item {
    font-weight: bold;
    font-size: 16px;
    line-height: 24px;
    color: #A3A3A3;
    display: flex;
    align-items: center;
    margin-right: 53px;
    margin-bottom: 15px;
}
.tags-point{
    display: inline-flex;
    align-items: center;
}
.tags-point:before{
    content: "";
    display: block;
    width: 6px;
    height: 6px;
    min-width: 6px;
    border-radius: 100%;
    background: #C4C4C4;
    margin-left: 5px;
    margin-right: 5px;
}
.proekt__content__right{
    flex: 0 0 255px;
}

.proekt__stat__status{
    font-weight: bold;
    font-size: 16px;
    line-height: 18px;
    color: #A3A3A3;
    margin-bottom: 6px;
    letter-spacing: 0.3px;

}
.proekt__stat__item{
    display: flex;
    align-items: center;
    font-size: 14px;
    line-height: 20px;
    color: #373F41;
    letter-spacing: 0.2px;
    margin-bottom: 8px;
    img{
        margin-right: 11px;
    }
}
.proekt__content__text{
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.2px;
    color: #373F41;
    margin-bottom: 40px;
}
.proekt__content__tags{
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    margin-bottom: 10px;
}

.proekt__content__tags__item{
    font-weight: bold;
    font-size: 14px;
    line-height: 20px;
    display: flex;
    align-items: center;
    text-align: center;
    color: #3C64B1;
    margin-right: 10px;
    padding: 8px 16px;
    background: #DBEEFB;
    border-radius: 20px;
    margin-bottom: 9px;
}
.proekt__content__bot{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.proekt__location{
    display: flex;
    align-items: flex-end;
    font-weight: bold;
    font-size: 14px;
    line-height: 20px;
    color: #4A4A4A;
    padding-right: 15px;
    svg{
        margin-right: 13px;
    }
}

.proekt__content__actions{
    flex: 0 0 255px;
    .btn{
        font-weight: bold;
        font-size: 16px;
        line-height: 24px;
        color: #F4F7FF;
        padding: 8px 20px;
    }
}
.proekt__chat{
    display: flex;
    align-content: center;
    font-weight: bold;
    font-size: 14px;
    line-height: 20px;
    color: #053FFA;
    margin-bottom: 20px;
    transition: 0.3s all;
    svg{
        margin-right: 4px;
    }
}
.proekt__chat:hover{
    opacity: 0.9;
}
.proekt__more{
    display: flex;
    justify-content: center;
    padding-top: 49px;
    padding-bottom: 90px;
    width: 100%;
    max-width: 920px;
    margin: 0 auto;
    border-top: 1px solid #E0E4EF;
    margin-top: 57px;
}

@media(max-width: 1300px){
    .filter{
        margin-right: 25px;
    }
    .proekt__search{
        margin-bottom: 15px;
    }
    .proekt__more{
        margin-top: 25px;
        padding-bottom: 50px;
    }
    .filter__checkboxes{
        margin-bottom: 25px;
    }
}

@media(max-width: 991px){
    .filter__content{
        display: none;
    }
    .proekt{
        flex-direction: column;
    }
    .filter{
        flex: auto;
        width: 100%;
    }
    .filter__title{
        position: relative;
        display: inline-block;
        padding-right: 35px;
        margin-bottom: 15px;
    }

    .filter__title:after{
        position: absolute;
        content: "";
        top: 15px;
        right: 10px;
        width: 0;
        height: 0;
        border: 6px solid transparent;
        border-color: #000 transparent transparent transparent;
    }
    .filter__title.active:after{
        border-color: transparent transparent #000 transparent;
        top: 12px;
    }
    .filter__tab{
        margin-bottom: 20px;
    }
    .filter__row{
        margin-bottom: 20px;
    }
    .filter__label{
        margin-bottom: 10px;
    }
    .proekt__tags__item{
        margin-bottom: 10px;
    }
}

@media(max-width:767px){
    .proekt__name{
        padding: 20px 20px;
    }
    .proekt__content{
        padding-right: 20px;
        padding-left: 20px;
    }
    .proekt__chat{
        margin-bottom: 15px;
    }
    .proekt__content__right{
        flex: 0 0 200px;
    }
    .proekt__content__text{
        margin-bottom: 25px;
    }
    .proekt__name{
        font-size: 24px;
        line-height: 1.3;
    }
    .proekt__tags__item{
        margin-bottom: 10px;
        margin-right: 20px;
    }
    .proekt__content__person__name{
        font-size: 14px;
    }
    
    .proekt__content__tags__item{
        padding: 6px 12px;
    }
}

@media(max-width:575px){
    .proekt__more .btn{
        font-size: 14px;
        line-height: 1.4;
        padding: 8px 20px;
    }
    .proekt__content__actions .btn{
        font-size: 14px;
        line-height: 1.4;
    }
    .proekt__content__top{
        flex-direction: column;
        margin-bottom: 0px;
    }
    .proekt__content__bot{
        flex-direction: column;
        align-items: flex-start;
    }
    .proekt__name{
        flex-direction: column;
        align-items: flex-start;
    }
    .proekt__actios{
        margin-left: 0;
        margin-top: 15px;
    }
    .proekt__actios button{
        margin-left: 0;
        margin-right: 10px;
    }
    .proekt__name{
        font-size: 18px;
    }
    .proekt__tags__item{
        font-size: 14px;
        line-height: 20px;
    }
    .proekt__stat__status{
        font-size: 14px;
        line-height: 1.3;
    }
    .proekt__content__right{
        flex: auto;
    }
    .proekt__content__left{
        padding-right: 0;
    }
    .proekt__location{
    }
    .proekt__content__actions{
        flex: auto;
        margin-top: 50px;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .proekt__more{
        padding-top: 25px;
        padding-bottom: 25px;
    }
    .proekt__content__tags__item{
        font-size: 12px;
        line-height: 1.3;
    }
    .proekt__stat__item{
        font-size: 12px;
        line-height: 1.3;
    }
    .proekt__stat__item img{
        margin-right: 5px;
    }
}

@media(max-width:374px){
    .proekt__more .btn{
        font-size: 13px;
    }
    .proekt__content__actions .btn{
        font-size: 13px;
    }
}

.proekt__list{
    background-color: #fff;
    border-radius: 15px;
    padding-right: 32px;
    padding-left: 32px;
}
.proekt__list__top{
    padding-top: 57px;
    font-weight: bold;
    font-size: 32px;
    line-height: 40px;
    color: #0A243F;
    padding-bottom: 10px;
   
}
.proekt__list{
    .proekt__content__person--list{
        .proekt__tags{
            margin-bottom: 0;
        }
        .proekt__content__person__name{
            font-weight: bold;
            font-size: 18px;
            line-height: 28px;
            color: #0A243F;
            margin-bottom: 8px;
            display: block;
        }
        .proekt__content__person__name:hover{
            text-decoration: underline;
        }
        
    }
    .proekt__content__right{
        flex: none;
    }
    .proekt__content__avatar{
        margin-bottom: 8px;
    }
    .proekt__content__text{
        max-width: 745px;
    }
    .proekt__content{
        padding: 0;
    }
    .proekt__chat{
        margin-bottom: 40px;
        svg{
           margin-left: 11px;
           transition: 0.3s all;
        }
    }
    .proekt__chat:hover{
        svg{
            transform: translateX(5px);
        }
    }
    .proekt__content__actions{
        display: flex;
        flex-direction: column;
        align-items: flex-end;
    }
    .proekt__more{
        max-width: initial;
        padding-bottom: 50px;
        margin: 0;
    }
    .proekt__content__tags{
        max-width: 700px;
    }
    
}

@media(min-width: 1300px){
    .proekt__list{
        .proekt__content__actions{
            margin-top: -45px;
        }
    }
}
.proekt__list__item{
    padding-top: 52px;
    padding-bottom: 40px;
    border-top: 1px solid #E0E4EF;
}

@media(max-width: 1300px){
    .proekt__list{
        .proekt__content__person{
            align-items: flex-start;
        }
    }
}

@media(max-width: 767px){
    .proekt__list {
        padding-right: 20px;
        padding-left: 20px;
        .proekt__list__item{
            padding-top: 25px;
            padding-bottom: 25px;
        }
        .proekt__chat{
            margin-bottom: 20px;
        }
        .proekt__list__top{
            padding-top: 20px;
        }
    }
}

@media(max-width: 575px){
    .proekt__list {
        .proekt__content__person--list .proekt__content__person__name {
            font-size: 14px;
            line-height: 1.4;
            min-height: 50px;
        }
        .proekt__content__person--list .proekt__tags{
            margin-left: -60px;
            margin-top: 20px;
        }
    }
    .proekt__list__top{
        font-size: 28px;
    }
}

.exp-content__blocks{
    flex: 1;
    width: calc(100% - 310px);
    .exp-content__edit{
        width: 100%;
        margin-bottom: 64px;
        min-height: 515px;
    }
}
.proekt__tabs{
    margin-top: 42px;
    border-bottom: 1px solid #A1A4B1;
    margin-bottom: 29px;
    ul{
        display: flex;
        padding: 0;
        margin: 0;
        overflow-x: auto;
        width: 100%;
        padding-bottom: 2px;
    }
    li{
        list-style: none;
        margin-right: 58px;
        margin-bottom: -2px;
        min-width: max-content;
    }
    a{
        font-size: 24px;
        line-height: 36px;
        text-align: center;
        display: block;
        color: #0A243F;
    }
    a:hover{
        color: #2B5CFB;
    }
    a.active{
        color: #2B5CFB;
        border-bottom:2px solid #2B5CFB;
    }
}

.my-proekts-filter{
    display: flex;
    align-items: center;   
    flex-wrap: wrap;
    justify-content: space-between;
    border-bottom: 1px solid #A1A4B1;
    margin-bottom: 25px;
    
}
.my-proekts-filter__search{
    position: relative;
    width: 320px;
    margin-bottom: 41px;
    margin-right: 15px;
    input {
        background: #FFFFFF;
        border-radius: 20px;
        height: 48px;
        padding-left: 40px;
        padding-right: 15px;
        font-size: 14px;
        line-height: 20px;
        border: 0;
        width: 100%;
        border: 1px solid #A1A4B1;
    }   
    button {
        position: absolute;
        left: 6px;
        top: 50%;
        transform: translateY(-50%);
        width: 35px;
        height: 35px;
        border: none;
        background-color: transparent;
        padding: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        border-radius: 100%;
        
    }
}
.my-proekts-filter__wrap{
    display: flex;
    align-content: center;   
    justify-content: space-between;
}
.my-proekts-filter__item{
    display: flex;
    align-items: center;
    margin-right: 15px;
    font-weight: bold;
    font-size: 18px;
    line-height: 24px;
    letter-spacing: 0.1px;
    color: #0A243F;
    font-family: 'Mulish';
    margin-bottom: 41px;
    margin-right: 50px;
    .select-selected{
        padding: 0;
        border: 0;
        border-radius: 0;
        margin-left: 5px;
        padding-right: 16px;
        font-size: 18px;
        line-height: 24px;
        color: #0A243F;
        letter-spacing: 0.1px;
        font-weight: 400;
        font-family: 'Mulish';
    }
    .select-selected:after{
        top: 10px;
        right: 0;
    }
    .select-selected.select-arrow-active:after{
        top: 4px;
    }
    .select-selected.select-arrow-active{
        box-shadow: none;
    }
    .select-items{
        width: 175px;
        right: auto;
    }
}

.my-proekts-filter__item:last-child{
    .select-items{
        width: 175px;
        left: auto;
        right: 0;
    }
}

.my-proekts-list__item{
    margin-bottom: 36px;
    padding-bottom: 20px;
    border-bottom: 1px solid #A1A4B1;
    .data{
        font-size: 16px;
        line-height: 18px;
        letter-spacing: 0.3px;
        color: #A3A3A3;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        font-family: 'Mulish';
        span{
            margin-left: 26px;
        }
    }
    .title{
        font-weight: bold;
        font-size: 24px;
        line-height: 36px;
        color: #0A243F;
        margin-bottom: 10px;
        display: block;
    }
    .title:hover{
        text-decoration: underline;
    }
    .text{
        font-size: 15px;
        line-height: 24px;
        color: rgba(0, 0, 0, 0.8);
        margin-bottom: 19px;
    }
    .detail{
        display: flex;
        justify-content: flex-end;
        a{
            display: flex;
            align-items: center;
            font-weight: bold;
            font-size: 14px;
            line-height: 28px;
            color: #2B5CFB;
            svg{
                margin-left: 11px;
                transition: 0.3s all;
            }
        }
        a:hover{
            svg{
                transform: translateX(5px);
            }
        }
    }
}
